.featured {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.featuredItem {
  flex: 1;
  margin: 0 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}
.featuredTitle {
  font-size: 20px;
}
.featuredMoneyContainer {
  margin: 10px 0;
  display: flex;
  align-items: center;
}
.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}
.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
.featuredIcon.positive {
  font-size: 14px;
  margin-left: 5px;
  color: green;
}
.featuredIcon.negative {
  font-size: 14px;
  margin-left: 5px;
  color: red;
}
.featuredSub{
    color: gray;
    font-size: 15px;
}